<template lang="pug">
  footer.bt-tab-bar
    div.tab-bar( 
      :class="{ 'active': $route.name === tab.link }"
      v-for="tab in tabs", 
      @click="$router.push({name: tab.link})" 
      )
      span.icon.iconfont( :class="[tab.icon]")
      div {{tab.text}}
   
</template>
<script>
export default {
  data() {
    return {
      tabs: [
        {
          text: '消息',
          icon: 'icon-xiaoxi',
          link: 'messages'
        },
        {
          text: '联系人',
          icon: 'icon-yonghu',
          link: 'linkman'
        },
        {
          text: '看点',
          icon: 'icon-kandian'
        },
        {
          text: '动态',
          icon: 'icon-dongtai'
        }
      ]
    }
  }
}
</script>
<style lang="stylus" scoped>
  .bt-tab-bar 
    box-sizing border-box
    width 100%
    position fixed
    bottom 0 
    z-index 40 
    left 0 
    background-color #ffffff
    display flex 
    border-top 1px solid #ececec
    .tab-bar 
      padding .425rem 0 .5rem 0
      flex 1
      color #807f8d
      text-align center
      font-size .5rem
      .icon 
        font-size 1.6rem
      &.active 
        color #47bafd
        .icon 
          color #47bafd 

</style>





